import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { IonicModule } from '@ionic/angular';
import { RouterModule, Routes } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { DashboardComponent } from './dashboard.component';

const routes: Routes = [
  {
    path: '',
    component: DashboardComponent,
    children: [
      {
        path: 'overviews',
        loadChildren: () => import('./overviews/overviews.module').then(m => m.OverviewsModule)
      },
      {
        path: 'manage-shop-free',
        loadChildren: () => import('./manage-shop-free/manage-shop-free.module').then(m => m.ManageShopFreeModule)
      },
      {
        path: 'manage-shop-payment',
        loadChildren: () => import('./manage-shop-payment/manage-shop-payment.module').then(m => m.ManageShopPaymentModule)
      },
      {
        path: 'manage-product',
        loadChildren: () => import('./manage-product/manage-product.module').then(m => m.ManageProductModule)
      },
      // {
      //   path: 'history-payment',
      //   loadChildren: () => import('./history-payment/history-payment.module').then(m => m.HistoryPaymentModule)
      // },
      // {
      //   path: 'manage-keys',
      //   loadChildren: () => import('./manage-keys/manage-keys.module').then(m => m.ManageKeysModule)
      // },
    ]
  },


];


@NgModule({
  declarations: [DashboardComponent],
  imports: [
    CommonModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule,
    NgZorroAntModule,
    TranslateModule,
    RouterModule.forChild(routes)
  ]
})
export class DashboardModule { }
